export const parseJsonString = <ResData = Record<string, unknown>>(
  string: string,
): ResData | null => {
  try {
    return JSON.parse(string);
  } catch (e) {
    console.error('[parseJsonString] error: ', e);
    return null;
  }
};

export const stringifyObject = (
  object: Record<string, unknown>,
): string | null => {
  try {
    return JSON.stringify(object);
  } catch (e) {
    console.error('[stringifyObject] error: ', e);
    return null;
  }
};
