import type { ComponentProps, FC } from 'react';
import cn from 'classnames';
import Paragraph from '../typography/paragraph.tsx';

export interface InputProps extends ComponentProps<'input'> {
  label?: string;
  error?: string | boolean;
}

const Input: FC<InputProps> = ({
  className,
  type,
  label,
  error,
  id,
  ...rest
}) => {
  const hasError = !!error;

  return (
    <div className='flex flex-col gap-2'>
      {!!label && (
        <label htmlFor={id} className={'mb-1 text-xs leading-3.5'}>
          {label}
        </label>
      )}
      <input
        id={id}
        type={type ?? 'text'}
        className={cn(
          'w-full transition bg-background-secondary rounded-[32px] px-4 py-2 h-9 text-xs border border-transparent text-foreground-secondary placeholder:text-foreground-secondary/40 outline-none',
          className,
          {
            '!border-error': hasError,
          },
        )}
        {...rest}
      />
      {hasError && (
        <Paragraph variant={'error'} size={'xs'}>
          {error}
        </Paragraph>
      )}
    </div>
  );
};

Input.displayName = 'Input';

export default Input;
