import { CSSProperties, FC, useEffect, useRef } from 'react';
import { Hourglass2Icon } from '../../icons';
import cn from 'classnames';

export interface ProgressCircleProps {
  delay?: number; // seconds
  initialProgress?: number;
  width?: number | string;
  classNameWrapper?: string;
  holdCircleColor: 'primary' | 'secondary';
  isReverse?: boolean;
  resetTrigger?: number;
}

const holdCircleColors = {
  primary: 'stroke-background-primary',
  secondary: 'stroke-background-secondary',
};

const ProgressCircle: FC<ProgressCircleProps> = ({
  initialProgress = 100, // seconds
  delay = 3,
  classNameWrapper,
  holdCircleColor = 'primary',
  width = 129,
  isReverse = false,
  resetTrigger,
}) => {
  const svgRef = useRef<SVGSVGElement>(null);

  useEffect(() => {
    if (!svgRef.current) {
      return;
    }

    svgRef.current.style.animation = 'none';

    setTimeout(() => {
      if (!svgRef.current) {
        return;
      }
      svgRef.current.style.setProperty(
        'animation',
        `progress-animation-circle ${delay}s linear 0s 1 forwards ${isReverse ? 'reverse' : ''}`,
      );
    }, 10);
  }, [resetTrigger, delay, isReverse]);

  return (
    <div className={cn('relative w-fit h-fit', classNameWrapper)}>
      <div
        style={{
          position: 'absolute',
          top: '50%',
          left: '50%',
          transform: 'translate(-50%, -50%)',
        }}
      >
        <Hourglass2Icon
          width={33}
          height={33}
          className={'text-foreground-primary'}
        />
      </div>

      <svg
        ref={svgRef}
        width={width}
        height={width}
        viewBox={`0 0 ${width} ${width}`}
        className='circular-progress'
        style={
          {
            '--progress': initialProgress,
            animation: `progress-animation-circle ${delay}s linear 0s 1 forwards ${isReverse ? 'reverse' : ''}`,
            '--size': `${width}px`,
          } as CSSProperties & Record<string, string | number>
        }
        fill='none'
        xmlns='http://www.w3.org/2000/svg'
      >
        <circle
          className={cn(
            'circular-progress-bg',
            holdCircleColors[holdCircleColor],
          )}
        />
        <circle className={cn('circular-progress-fg')} />
      </svg>
    </div>
  );
};

ProgressCircle.displayName = 'ProgressCircle';

export default ProgressCircle;
