import { useEffect, useReducer } from 'react';
import { Storage } from '../utils/storage.ts';
import { IProduct } from '../types/product.ts';
import { STORAGE_KEYS } from '../constants/storage-keys.ts';
import { IWidgetOptions } from '../types/widget-options.ts';

type IWidgetStateProps = Pick<
  IWidgetOptions,
  'shopCurrency' | 'customerName' | 'product' | 'onStartAuction'
> & {
  setSocketUrl: (url: string | undefined) => void;
};

interface WidgetState {
  isOpen: boolean;
  isInfoOpen: boolean;
  isAuctionStarting: boolean;
  currency?: string;
  customerName?: string | null;
  cart: IProduct[];
}

type WidgetAction =
  | { type: 'SET_IS_AUCTION_STARTING'; payload: boolean }
  | { type: 'SET_CUSTOMER_NAME'; payload: string }
  | { type: 'SET_IS_OPEN'; payload: boolean }
  | { type: 'SET_IS_INFO_OPEN'; payload: boolean }
  | { type: 'SET_CURRENCY'; payload: string | undefined }
  | { type: 'SET_CART'; payload: IProduct[] }
  | { type: 'ADD_TO_CART'; payload: IProduct };

const initialState: WidgetState = {
  isOpen: false,
  isInfoOpen: false,
  isAuctionStarting: false,
  currency: 'USD',
  customerName: null,
  cart: [],
};

const addToCart = (product: IProduct): IProduct[] | null => {
  try {
    const storageCart = Storage.get<IProduct[]>(STORAGE_KEYS.CART);
    const newCart = storageCart ? [...storageCart] : [];
    if (!storageCart) {
      newCart.push(product);
      Storage.set(STORAGE_KEYS.CART, newCart);
      return newCart;
    }

    const hasInCart = newCart.find((item) => item.id === product.id);
    if (hasInCart) {
      return newCart;
    }

    newCart.push(product);
    Storage.set(STORAGE_KEYS.CART, newCart);

    return newCart;
  } catch (error) {
    console.warn('[HobuyWidget] Error adding product to cart:', error);
    return null;
  }
};

function widgetReducer(state: WidgetState, action: WidgetAction): WidgetState {
  switch (action.type) {
    case 'SET_IS_AUCTION_STARTING':
      return { ...state, isAuctionStarting: action.payload };
    case 'SET_IS_OPEN':
      return { ...state, isOpen: action.payload };
    case 'SET_CUSTOMER_NAME':
      return { ...state, customerName: action.payload };
    case 'SET_IS_INFO_OPEN':
      return { ...state, isInfoOpen: action.payload };
    case 'SET_CURRENCY':
      return { ...state, currency: action.payload };
    case 'SET_CART':
      return { ...state, cart: action.payload };
    case 'ADD_TO_CART':
      if (state.cart.some((item) => item.id === action.payload.id))
        return state;
      return {
        ...state,
        isOpen: true,
        cart: addToCart(action.payload) || state.cart,
      };
    default:
      return state;
  }
}

export const useWidgetState = ({
  product,
  shopCurrency,
  customerName,
  onStartAuction,
  setSocketUrl,
}: IWidgetStateProps) => {
  const [state, dispatch] = useReducer(widgetReducer, initialState);

  useEffect(() => {
    const storedCart = Storage.get<IProduct[]>(STORAGE_KEYS.CART);
    if (storedCart) {
      dispatch({ type: 'SET_CART', payload: storedCart });
    }
  }, []);

  useEffect(() => {
    if (shopCurrency) {
      dispatch({ type: 'SET_CURRENCY', payload: shopCurrency });
    }
  }, [shopCurrency]);

  useEffect(() => {
    if (customerName) {
      dispatch({ type: 'SET_CUSTOMER_NAME', payload: customerName });
    }
  }, [customerName]);

  const handleAddVariationToCart = () => {
    if (!product) {
      console.warn('[HobuyWidget] No product provided to add to cart');
      return;
    }
    try {
      dispatch({ type: 'SET_IS_OPEN', payload: true });
      dispatch({ type: 'ADD_TO_CART', payload: product });
    } catch (e: unknown) {
      console.warn('[HobuyWidget] Error adding product to cart:', e);
    }
  };

  const handleSetIsOpen = (v: boolean) =>
    dispatch({ type: 'SET_IS_OPEN', payload: v });
  const handleSetIsInfoOpen = (v: boolean) =>
    dispatch({ type: 'SET_IS_INFO_OPEN', payload: v });

  const handleStartAuction = async (name: string) => {
    if (!onStartAuction) {
      return;
    }

    dispatch({ type: 'SET_IS_AUCTION_STARTING', payload: true });

    const result = await onStartAuction(state.cart ?? [], name);
    dispatch({ type: 'SET_IS_AUCTION_STARTING', payload: false });
    console.log('[handleStartAuction] result: ', result);

    if (!result) {
      console.warn('[HobuyWidget] onStartAuction did not return a result');
      return;
    }

    console.log('[handleStartAuction] result: ', result);
    setSocketUrl(result.socketUrl);
  };

  return {
    state,
    handleSetIsOpen,
    handleSetIsInfoOpen,
    handleAddVariationToCart,
    handleStartAuction,
  };
};
