import { FC, useContext, useMemo, useState } from 'react';
import Button from '../ui/buttons/button.tsx';
import Input from '../ui/form/input.tsx';
import { WidgetContext } from '../lib/context.ts';
import Title from '../ui/typography/title.tsx';
import { getPriceWithCurrency } from '../utils/currency';
import Logo from '../ui/logo.tsx';
import ListProducts from '../components/list-products.tsx';
import { useI18n } from '../hooks/use-i18n.ts';
import Paragraph from '../ui/typography/paragraph.tsx';

export interface AuctionCartProps {}

const AuctionCart: FC<AuctionCartProps> = () => {
  const {
    isAuctionStarting,
    cart,
    customerName,
    currency,
    setIsOpen,
    handleStartAuction,
  } = useContext(WidgetContext);
  const [name, setName] = useState<string>(customerName ?? '');
  const [isFieldTouched, setIsFieldTouched] = useState<boolean>(false);
  const { t } = useI18n();

  const totalPrice =
    cart?.reduce((acc, item) => {
      const priceNum = item.price ? +item.price : 0;

      return acc + (!isNaN(priceNum) ? priceNum : 0);
    }, 0) ?? 0;

  const productsList = useMemo(() => {
    return (
      cart?.map((item) => ({
        imgUrl: item.imageUrl,
        imgAlt: item.name,
        price: item.price ?? null,
        currencyCode: currency ?? '',
        userName: customerName ?? null,
      })) ?? []
    );
  }, [cart, currency, customerName]);

  const handleStart = async () => {
    handleStartAuction(name);
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setName(e.target.value);
  };

  const handleInputFocus = () => {
    if (isFieldTouched) {
      return;
    }

    setIsFieldTouched(true);
  };

  return (
    <div className='widget-screen bg-background-primary text-foreground-primary'>
      <div className='widget-screen-content'>
        <div className='flex justify-between items-center'>
          <Title>
            {t('cart.title')}
            {/*Auction cart*/}
          </Title>
        </div>

        <div className='flex flex-col '>
          <div className='flex flex-col gap-4 w-full'>
            <ListProducts list={productsList} />

            <hr className='border-t border-border' />

            <div className='flex items-center justify-between'>
              <div className='text-xs'>
                {t('cart.total-start-price')}
                {/*Total starting price*/}
              </div>
              <Paragraph className='text-xl font-semibold'>
                {totalPrice > 0 &&
                  getPriceWithCurrency(totalPrice, currency ?? 'USD')}
              </Paragraph>
            </div>

            <Input
              // label={'Enter your name to take part in auction'}
              // placeholder='Your name'
              // error={'Something went wrong'}

              name={'userName'}
              type={'text'}
              id={'userName'}
              label={t('cart.input.label')}
              placeholder={t('cart.input.placeholder')}
              error={!name && isFieldTouched ? t('cart.input.error') : false}
              value={name}
              onChange={handleInputChange}
              onFocus={handleInputFocus}
              autoComplete={'name'}
            />
            <hr className='border-t border-border' />

            <div className='flex flex-col gap-2'>
              <Button
                disabled={!name || isAuctionStarting}
                onClick={handleStart}
              >
                {t(
                  isAuctionStarting
                    ? 'cart.start-auction.loading'
                    : 'cart.start-auction',
                )}
                {/*Instant Auction*/}
              </Button>
              <Button variant={'secondary'} onClick={() => setIsOpen(false)}>
                {t('cart.to-catalog')}
                {/*Back to catalog*/}
              </Button>
            </div>
          </div>
        </div>
      </div>

      <Logo />
    </div>
  );
};

AuctionCart.displayName = 'AuctionCart';

export default AuctionCart;
