import { FC, useContext, useMemo } from 'react';
import Button from '../ui/buttons/button.tsx';
import { WidgetContext } from '../lib/context.ts';
import Title from '../ui/typography/title.tsx';
import { getPriceWithCurrency } from '../utils/currency';
import Logo from '../ui/logo.tsx';
import ListProducts from '../components/list-products.tsx';
import Paragraph from '../ui/typography/paragraph.tsx';
import { useI18n } from '../hooks/use-i18n.ts';
import { isNullable } from '../utils/is-nullable.ts';

export interface AuctionCartProps {}

const AuctionResults: FC<AuctionCartProps> = () => {
  const { setIsOpen, auctionState, handleResetAuction, handleUseWinData } =
    useContext(WidgetContext);
  const { t } = useI18n();

  const isWinner =
    auctionState.isFirstRoundWinner || auctionState.isSecondRoundWinner;

  const results = useMemo(() => {
    const products = auctionState.selectionMessage?.data.products ?? [];
    const product = products[0];

    return [
      {
        imgUrl: product?.imageUrl,
        imgAlt: product?.name,
        price: auctionState.winnerPrice,
        currencyCode: auctionState.selectionMessage?.data.currency ?? 'USD',
        userName: auctionState.currentCustomer?.name,
        userLocation: auctionState.currentCustomer?.location,
      },
    ];
  }, [auctionState]);

  const handleTryAgain = () => {
    handleResetAuction();
    setIsOpen(false);
  };

  return (
    <div className='widget-screen bg-background-primary text-foreground-primary'>
      <div className='widget-screen-content'>
        <Title>
          {t('auction-results.title')}
          {/*Auction results*/}
        </Title>

        <div className='flex flex-col gap-2'>
          <Title size={'3xl'} variant={'gradient'} tag={'h3'}>
            {t(
              isWinner ? 'auction-results.you-win' : 'auction-results.you-lose',
            )}
            {/*{isWinner ? 'You have won!' : 'The other one won!'}*/}
          </Title>
          <Paragraph size={'lg'} variant={'gray'}>
            {t('auction-results.description')}
            {/*Lorem ipsum dolor sit amet consectetur. Neque ipsum enim bibendum*/}
            {/*nisl ipsum*/}
          </Paragraph>
        </div>
        <div className='flex flex-col '>
          <div className='flex flex-col gap-4 w-full'>
            <ListProducts list={results} />

            {isWinner && (
              <>
                <hr className='border-t border-border' />

                <div className={'flex flex-col gap-2'}>
                  <div className='flex items-center justify-between'>
                    <Paragraph size={'xs'}>
                      {t('auction-results.prev-price')}
                      {/*Previous total price*/}
                    </Paragraph>
                    <Paragraph size={'xs'}>
                      {!isNullable(auctionState.selectionMessage?.data.price) &&
                        getPriceWithCurrency(
                          auctionState.selectionMessage?.data.price as number,
                          auctionState.selectionMessage?.data.currency,
                        )}
                    </Paragraph>
                  </div>

                  <div className='flex items-center justify-between'>
                    <Paragraph size={'xs'}>
                      {t('auction-results.current-price')}
                      {/*Current total price*/}
                    </Paragraph>
                    <Title size={'xl'} tag={'p'}>
                      {!isNullable(auctionState.winnerPrice) &&
                        getPriceWithCurrency(
                          auctionState.winnerPrice,
                          auctionState.selectionMessage?.data.currency,
                        )}
                    </Title>
                  </div>
                </div>
              </>
            )}

            <hr className='border-t border-border' />

            <div className='flex flex-col gap-2'>
              {isWinner && (
                <Button onClick={handleUseWinData}>
                  {t('buttons.buy-this-price')}
                  {/*Buy at this price*/}
                </Button>
              )}
              <Button variant={'secondary'} onClick={handleTryAgain}>
                {t('buttons.try-again')}
                {/*Try again*/}
              </Button>
            </div>
          </div>
        </div>
      </div>

      <Logo />
    </div>
  );
};

AuctionResults.displayName = 'AuctionResults';

export default AuctionResults;
