import type { ComponentProps, FC } from 'react';
import cn from 'classnames';

type ButtonVariants = 'primary' | 'secondary' | 'icon';

export interface ButtonProps extends ComponentProps<'button'> {
  variant?: ButtonVariants;
}

const buttonVariants: Record<ButtonVariants | 'root', string> = {
  root: 'flex items-center justify-center cursor-pointer text-base font-semibold gap-2 transition',
  primary:
    'px-5 py-3 bg-accent h-11 text-foreground-accent border-none rounded-[32px]',
  secondary: 'h-11 text-foreground-primary border border-border rounded-[32px]',
  icon: '',
};

const Button: FC<ButtonProps> = ({
  variant = 'primary',
  className,
  children,
  disabled,
  onClick,
  onKeyDown,
  ...rest
}) => {
  const handleClick = (e: React.MouseEvent<HTMLButtonElement>) => {
    if (disabled) {
      e.preventDefault();
      e.stopPropagation();
      return;
    }
    onClick?.(e);
  };

  const handleKeyDown = (e: React.KeyboardEvent<HTMLButtonElement>) => {
    if (disabled && (e.key === 'Enter' || e.key === ' ')) {
      e.preventDefault();
      e.stopPropagation();
    }

    onKeyDown?.(e);
  };

  return (
    <button
      className={cn(buttonVariants.root, buttonVariants[variant], className, {
        'cursor-not-allowed opacity-50': disabled,
        'hover:bg-opacity-80 active:bg-opacity-80': !disabled,
      })}
      onClick={handleClick}
      onKeyDown={handleKeyDown}
      {...rest}
    >
      {children}
    </button>
  );
};

Button.displayName = 'Button';

export default Button;
