import en from './locales/en';
import uk from './locales/uk';

type Translation = Record<string, string | undefined>;
type LanguageChangeListener = (newLanguage: string) => void;

interface I18nConfig {
  translations: Record<string, Translation>;
  defaultLocale: string;
  locales: string[];
}

const config: I18nConfig = {
  translations: { en, uk },
  defaultLocale: 'en',
  locales: ['en', 'uk'],
};

const i18n = {
  config,
  _currentLanguage: config.defaultLocale,
  _listeners: new Set<LanguageChangeListener>(),

  t: function (key: string, lang?: string) {
    const language = lang || this._currentLanguage;
    const dictionary =
      config.translations[language] ||
      config.translations[config.defaultLocale];

    return dictionary[key] || key;
  },

  setLang: function (lang: string) {
    if (config.locales.includes(lang) && this._currentLanguage !== lang) {
      this._currentLanguage = lang;
      this._notifyListeners(lang);
    } else if (!config.locales.includes(lang)) {
      console.log('[Widget i18n] Language is not supported:', lang);
    }
  },

  subscribe: function (listener: LanguageChangeListener) {
    this._listeners.add(listener);

    return () => {
      this._listeners.delete(listener);
    };
  },

  // Сповіщення всіх підписників про зміну мови
  _notifyListeners: function (newLanguage: string) {
    this._listeners.forEach((listener) => listener(newLanguage));
  },

  get lang() {
    return this._currentLanguage;
  },

  set lang(newLang: string) {
    this.setLang(newLang);
  },
};

export default i18n;
