import type { FC, ReactNode } from 'react';
import Button from '../buttons/button.tsx';
import { BucketIcon } from '../../icons';
import { getPriceWithCurrency } from '../../utils/currency';
import Title from '../typography/title.tsx';
import cn from 'classnames';
import { isNullable } from '../../utils/is-nullable.ts';

export interface ProductProps {
  imgUrl?: string;
  imgAlt?: string;
  price: number | string | null;
  currencyCode: string;
  title?: string | ReactNode;
  type?: 'cart' | 'bidding';
  onRemove?: () => void;
  isHigher?: boolean;
}

const Product: FC<ProductProps> = ({
  imgUrl,
  imgAlt,
  currencyCode = 'USD',
  title = '',
  type = 'cart',
  price = 0,
  isHigher,
}) => {
  return (
    <div
      className={cn('relative', {
        'border-transparent p-[1px] before:absolute before:z-[1] before:rounded-[17px] before:bg-gradient-primary before:top-0 before:left-0 before:w-full before:h-full':
          isHigher,
        'border border-border rounded-2xl': !isHigher,
      })}
    >
      <div className='relative z-[2] p-4 rounded-2xl bg-background-primary text-foreground-primary flex items-center gap-3 w-full justify-between'>
        <div
          className={cn('flex w-full items-center', {
            'gap-3': type === 'cart',
            'gap-2': type === 'bidding',
          })}
        >
          <img
            className='h-[60px] w-[60px] object-contain'
            src={
              imgUrl ??
              'https://placeholder.pics/images/icons/apple-icon-60x60.png'
            }
            alt={imgAlt ?? 'Auction Item'}
          />
          <div className='flex flex-col flex-grow gap-1.5'>
            <Title tag={'h3'} size={'sm'}>
              {title}
            </Title>
            <Title tag={'h4'}>
              {!isNullable(price) && getPriceWithCurrency(price, currencyCode)}
            </Title>
          </div>
        </div>

        {type === 'cart' && (
          <Button variant={'icon'}>
            <BucketIcon width='24' height='24' />
          </Button>
        )}
      </div>
    </div>
  );
};

Product.displayName = 'Product';

export default Product;
