import currenciesData from './common-currency';

export type CurrencyType = {
  symbol: string;
  name: string;
  symbol_native: string;
  decimal_digits: number;
  rounding: number;
  code: string;
  name_plural: string;
};

export const getCurrency = (code: string = 'USD'): CurrencyType | undefined => {
  return (currenciesData as Record<string, CurrencyType>)[code];
};

export function formatPrice(price: string | number): string {
  const numPrice = typeof price === 'string' ? parseFloat(price) : price;

  return numPrice.toFixed(2);
}

export const getPriceWithCurrency = (
  price: string | number,
  code: string = 'USD',
): string => {
  const currency = getCurrency(code);

  return `${currency?.symbol ?? code}${formatPrice(price)}`;
};
