import { FC, useContext, useMemo } from 'react';
import Title from '../ui/typography/title.tsx';
import Paragraph from '../ui/typography/paragraph.tsx';
import ProgressLine from '../ui/progress/progress-line.tsx';
import Logo from '../ui/logo.tsx';
import { WidgetContext } from '../lib/context.ts';
import ListProducts from '../components/list-products.tsx';
import { IWSEventStageWaitRoundData } from '../types/ws-events.ts';
import { useI18n } from '../hooks/use-i18n.ts';

export interface BiddingPreparationProps {}

const BiddingPreparation: FC<BiddingPreparationProps> = () => {
  const { auctionState } = useContext(WidgetContext);
  const { t } = useI18n();

  const list = useMemo(() => {
    const { members } =
      (auctionState.waitRoundMessage?.data as IWSEventStageWaitRoundData) ?? {};

    if (!members || !members.length) {
      return [];
    }

    return members.map((member) => ({
      imgUrl: member?.products[0]?.imageUrl,
      imgAlt: member?.products[0]?.name,
      price: null,
      currencyCode: member.currency,
      userName: member.name,
      userLocation: member.location,
    }));
  }, [auctionState]);

  return (
    <div className='widget-screen'>
      <div className='widget-screen-content'>
        <Title>
          {t('bidding-preparation.title')}
          {/*Get ready to start bidding*/}
        </Title>

        <div>
          <Title size={'md'} tag={'h3'}>
            {t('bidding-preparation.subtitle')}
            {/*Participants*/}
          </Title>
          <Paragraph size={'xs'} variant={'gray'}>
            {t('bidding-preparation.description')}
            {/*Waiting for more participants...*/}
          </Paragraph>
        </div>

        <ProgressLine duration={30} />

        <ListProducts list={list} />
      </div>

      <Logo />
    </div>
  );
};

BiddingPreparation.displayName = 'BiddingPreparation';

export default BiddingPreparation;
