import type { FC } from 'react';
import Product from '../ui/cards/product.tsx';
import Paragraph from '../ui/typography/paragraph.tsx';

export interface ListProductsProps {
  list: {
    imgUrl: string;
    imgAlt: string;
    price: string | number | null;
    currencyCode: string;
    userName?: string | null;
    userLocation?: string | null;
    isHigher?: boolean;
  }[];
}

const ListProducts: FC<ListProductsProps> = ({ list }) => {
  return (
    <div className='flex flex-col gap-2'>
      {list.map(({ userName, userLocation, ...product }, index) => (
        <Product
          key={product.imgUrl + index}
          title={
            <>
              {userName && (
                <Paragraph size={'xs'} tag={'span'}>
                  {userName}
                </Paragraph>
              )}
              {userLocation && (
                <Paragraph size={'xs'} variant={'gray'} tag={'span'}>
                  {' '}
                  · {userLocation}
                </Paragraph>
              )}
            </>
          }
          type={'bidding'}
          {...product}
        />
      ))}
    </div>
  );
};

ListProducts.displayName = 'ListProducts';

export default ListProducts;
