import type { FC } from 'react';
import Button from '../ui/buttons/button.tsx';
import { useI18n } from '../hooks/use-i18n.ts';

export interface LocaleToggleProps {
  className?: string;
}

const LocaleToggle: FC<LocaleToggleProps> = ({ className }) => {
  const { setLang, lang } = useI18n();
  const isEnglish = lang === 'en';

  const handleToggleLocale = () => {
    setLang(isEnglish ? 'uk' : 'en');
  };

  return (
    <Button variant={'icon'} className={className} onClick={handleToggleLocale}>
      {isEnglish ? 'uk' : 'en'}
    </Button>
  );
};

LocaleToggle.displayName = 'LocaleToggle';

export default LocaleToggle;
