import { createContext } from 'react';
import { IAuctionState, initialAuctionState } from '../hooks/use-auction.ts';
import { IProduct } from '../types/product.ts';

interface WidgetContextType {
  auctionSocket: WebSocket | null;
  auctionState: IAuctionState;
  cart: IProduct[];
  currency?: string | null;
  customerName?: string | null;

  isOpen: boolean;
  isInfoOpen: boolean;
  isAuctionStarting: boolean;

  setIsOpen: (isOpen: boolean) => void;
  setIsInfoOpen: (isInfoOpen: boolean) => void;
  handleResetAuction: () => void;
  handleAuctionSendMessage: (
    message: Record<string, unknown>,
  ) => boolean | undefined;
  handleAddVariationToCart: () => void;
  handleStartAuction: (name: string) => Promise<void> | void;
  handleUseWinData: () => void;
}

export const WidgetContext = createContext<WidgetContextType>({
  auctionSocket: null,
  auctionState: initialAuctionState,
  cart: [],
  currency: null,
  customerName: null,

  isOpen: false,
  isInfoOpen: false,
  isAuctionStarting: false,

  setIsOpen: () => undefined,
  setIsInfoOpen: () => undefined,
  handleResetAuction: () => undefined,
  handleAuctionSendMessage: () => false,
  handleAddVariationToCart: () => undefined,
  handleStartAuction: () => undefined,
  handleUseWinData: () => undefined,
});
