import { useEffect, useReducer, useState } from 'react';
import {
  parseJsonString,
  stringifyObject,
} from '../utils/parse-json-string.ts';
import {
  IWSEvent,
  IWSEventStageRoundStep,
  IWSEventStageRoundStepSelfBet,
  IWSEventStageWaitRound,
  IWSEventStepSelection,
  IWSEventWin,
} from '../types/ws-events.ts';
import { WS_EVENT_ENUM } from '../constants/ws-event-enum.ts';
import { STAGE_ENUM, STAGE_SCREENS_ENUM } from '../constants/stage-enum.ts';
import { IMember } from '../types/member.ts';
import { StageScreensType } from '../types/screens.ts';
import { Storage } from '../utils/storage.ts';

export interface IAuctionState {
  isAuctionRunning: boolean;
  isFirstRoundWinner: boolean;
  isSecondRoundWinner: boolean;
  winnerPrice: number;
  currentStage: StageScreensType | null;
  currentCustomer: IMember | null;

  error?: string | null;
  errorEvent?: string | null;
  errorCode?: string | null;

  selectionMessage: IWSEventStepSelection | null;
  waitRoundMessage: IWSEventStageWaitRound | null;
  roundStepMessage: IWSEventStageRoundStep | null;
  selfBetMessage: IWSEventStageRoundStepSelfBet | null;

  winData: unknown;
}

type IAuctionActions =
  | {
      type: 'SET_IS_FIRST_ROUND_WINNER';
      payload: { isWinner: boolean; price: number };
    }
  | {
      type: 'SET_IS_SECOND_ROUND_WINNER';
      payload: { isWinner: boolean; price: number };
    }
  | { type: 'SET_ERROR'; payload: IWSEvent }
  | { type: 'SET_WIN_DATA'; payload: unknown }
  | { type: 'RESET_ERROR' }
  | { type: 'SET_CURRENT_STAGE'; payload: IAuctionState['currentStage'] }
  | { type: 'SET_SELECTION_MESSAGE'; payload: IWSEventStepSelection }
  | { type: 'SET_WAIT_ROUND_MESSAGE'; payload: IWSEventStageWaitRound }
  | { type: 'SET_ROUND_MESSAGE'; payload: IWSEventStageRoundStep }
  | { type: 'SET_SELF_BET'; payload: IWSEventStageRoundStepSelfBet }
  | { type: 'RESET' };

export const initialAuctionState: IAuctionState = {
  isAuctionRunning: false,
  isSecondRoundWinner: false,
  isFirstRoundWinner: false,
  winnerPrice: 0,
  currentStage: null,

  currentCustomer: null,
  selectionMessage: null,
  waitRoundMessage: null,
  roundStepMessage: null,
  selfBetMessage: null,
  error: null,
  errorEvent: null,
  errorCode: null,

  winData: null,
};

const getCurrentCustomer = (
  state: IAuctionState,
  message: IWSEventStageWaitRound,
) => {
  const currentCustomerId = state.selectionMessage?.data.sessionId;

  return (
    message?.data.members.find((m) => m.sessionId === currentCustomerId) ||
    state.currentCustomer
  );
};

const reducer = (state: IAuctionState, action: IAuctionActions) => {
  if (state.isFirstRoundWinner) {
    return state;
  }

  switch (action.type) {
    case 'SET_CURRENT_STAGE':
      return { ...state, currentStage: action.payload };
    case 'SET_SELECTION_MESSAGE':
      return {
        ...state,
        selectionMessage: action.payload,
        isAuctionRunning: true,
      };
    case 'SET_WAIT_ROUND_MESSAGE':
      return {
        ...state,
        waitRoundMessage: action.payload,
        currentCustomer: getCurrentCustomer(state, action.payload),
      };
    case 'SET_ROUND_MESSAGE':
      return {
        ...state,
        roundStepMessage: action.payload,
      };
    case 'SET_IS_FIRST_ROUND_WINNER':
      return {
        ...state,
        isFirstRoundWinner: action.payload.isWinner,
        winnerPrice: action.payload.price,
      };

    case 'SET_IS_SECOND_ROUND_WINNER':
      return {
        ...state,
        isSecondRoundWinner: action.payload.isWinner,
        winnerPrice: action.payload.price,
      };

    case 'SET_SELF_BET':
      return {
        ...state,
        selfBetMessage: action.payload,
      };
    case 'SET_ERROR':
      return {
        ...state,
        error: action.payload.error || null,
        errorCode: action.payload.errorCode || null,
        errorEvent: action.payload.event || null,
      };

    case 'SET_WIN_DATA':
      return {
        ...state,
        winData: action.payload,
      };

    case 'RESET_ERROR':
      return {
        ...state,
        error: null,
        errorCode: null,
        errorEvent: null,
      };
    case 'RESET':
      return initialAuctionState;
    default:
      return state;
  }
};

export const useAuction = ({ url }: { url?: string }) => {
  const [auctionSocket, setAuctionSocket] = useState<WebSocket | null>(null);
  const [auctionState, setAuctionState] = useReducer(
    reducer,
    initialAuctionState,
  );

  useEffect(() => {
    if (!url) {
      return;
    }

    const socket = new WebSocket(url.trim());
    setAuctionSocket(socket);

    socket.onmessage = (eventMsg: MessageEvent) => {
      const msg = parseJsonString<IWSEvent>(eventMsg.data);
      if (!msg) return;
      if (Object.values(STAGE_SCREENS_ENUM).includes(msg.stage)) {
        setAuctionState({ type: 'SET_CURRENT_STAGE', payload: msg.stage });
        Storage.set('current_stage', msg.stage);
      }

      if (msg.error || msg.errorCode) {
        setAuctionState({ type: 'SET_ERROR', payload: msg });
        Storage.set('error', msg.stage);
      }

      if (
        msg.event == WS_EVENT_ENUM.CONNECTION_SERVICE &&
        msg.stage == STAGE_ENUM.SELECTION
      ) {
        setAuctionState({
          type: 'SET_SELECTION_MESSAGE',
          payload: msg as IWSEventStepSelection,
        });
        Storage.set('selection_message', msg);
      }

      if (
        (msg.event == WS_EVENT_ENUM.CONNECTION_AUCTION &&
          msg.stage == STAGE_ENUM.WAIT_FIRST_ROUND) ||
        (msg.event == WS_EVENT_ENUM.CONNECTION_AUCTION &&
          msg.stage == STAGE_ENUM.WAIT_SECOND_ROUND) ||
        msg.event == WS_EVENT_ENUM.CHANGE_MEMBERS
      ) {
        setAuctionState({
          type: 'SET_WAIT_ROUND_MESSAGE',
          payload: msg as IWSEventStageWaitRound,
        });
        Storage.set('wait_round_message', msg);
      }

      if (msg.event == WS_EVENT_ENUM.START || msg.event == WS_EVENT_ENUM.STEP) {
        setAuctionState({
          type: 'SET_ROUND_MESSAGE',
          payload: msg as IWSEventStageRoundStep,
        });
        Storage.set('round_message', msg);
      }

      if (
        msg.event == WS_EVENT_ENUM.SELF_WIN_BET &&
        msg.stage == STAGE_ENUM.SECOND_ROUND
      ) {
        setAuctionState({
          type: 'SET_SELF_BET',
          payload: msg as IWSEventStageRoundStepSelfBet,
        });
        Storage.set('self_bet', msg);
      }

      if (
        msg.event == WS_EVENT_ENUM.YOU_WIN &&
        msg.stage == STAGE_ENUM.FIRST_ROUND
      ) {
        setAuctionState({
          type: 'SET_IS_FIRST_ROUND_WINNER',
          payload: {
            isWinner: true,
            price: (msg as IWSEventWin).data?.price || 0,
          },
        });
        Storage.set('first_round_winner', {
          isWinner: true,
          price: (msg as IWSEventWin).data?.price || 0,
        });
      }

      if (
        msg.event == WS_EVENT_ENUM.YOU_WIN &&
        msg.stage == STAGE_ENUM.SECOND_ROUND
      ) {
        setAuctionState({
          type: 'SET_IS_SECOND_ROUND_WINNER',
          payload: {
            isWinner: true,
            price: (msg as IWSEventWin).data?.price || 0,
          },
        });
        Storage.set('second_round_winner', {
          isWinner: true,
          price: (msg as IWSEventWin).data?.price || 0,
        });
      }

      // Юзер виграв/не виграв у другому раунді встановлюється екран результатів
      if (
        (msg.event == WS_EVENT_ENUM.OTHER_WIN ||
          msg.event == WS_EVENT_ENUM.YOU_WIN ||
          msg.event == WS_EVENT_ENUM.NOT_WIN) &&
        msg.stage == STAGE_ENUM.SECOND_ROUND
      ) {
        setAuctionState({
          type: 'SET_CURRENT_STAGE',
          payload: STAGE_SCREENS_ENUM.RESULTS,
        });
        Storage.clear();
        socket.close();
      }

      if (msg.event == WS_EVENT_ENUM.STORE_WIN_DATA) {
        setAuctionState({
          type: 'SET_WIN_DATA',
          payload: {
            isWinner: true,
            price: (msg as IWSEventWin).data?.price || 0,
          },
        });
        Storage.set('win_data', {
          isWinner: true,
          price: (msg as IWSEventWin).data?.price || 0,
        });
      }
    };

    return () => {
      socket.close();
    };
  }, [url]);

  const handleAuctionSendMessage = (message: Record<string, unknown>) => {
    if (!auctionSocket) {
      console.error('WebSocket is not connected');
      return;
    }

    const msg = stringifyObject(message);
    if (!msg) {
      return;
    }
    setAuctionState({
      type: 'RESET_ERROR',
    });

    try {
      auctionSocket.send(msg);
      return false;
    } catch (error) {
      console.error('[handleAuctionSendMessage] error: ', error);
      return true;
    }
  };

  const handleResetAuction = () => {
    setAuctionState({ type: 'RESET' });
    auctionSocket?.close();
    setAuctionSocket(null);
  };

  return {
    auctionSocket,
    auctionState,
    handleAuctionSendMessage,
    handleResetAuction,
  };
};
