import { useCallback, useContext, useRef } from 'react';
import { WidgetContext } from '../lib/context';
import FloatTriggers from './float-triggers.tsx';
import { XIcon } from '../icons';
import Button from '../ui/buttons/button.tsx';
import AuctionCart from '../screens/auction-cart.tsx';
import BiddingPreparation from '../screens/bidding-preparation.tsx';
import cn from 'classnames';
import AuctionBiddingFirstRound from '../screens/auction-bidding-first-round.tsx';
import AuctionBiddingSecondRound from '../screens/auction-bidding-second-round.tsx';
import AuctionResults from '../screens/auction-results.tsx';
import { STAGE_SCREENS_ENUM } from '../constants/stage-enum.ts';
import LocaleToggle from './locale-toggle.tsx';

export function Widget() {
  const { auctionState, isOpen, setIsOpen } = useContext(WidgetContext);
  const wrapperRef = useRef<HTMLDivElement>(null);

  const isDoubleScreen =
    auctionState.currentStage === STAGE_SCREENS_ENUM.SECOND_ROUND ||
    auctionState.currentStage === STAGE_SCREENS_ENUM.FIRST_ROUND;

  const Screen = useCallback(() => {
    switch (auctionState.currentStage) {
      case STAGE_SCREENS_ENUM.RESULTS:
        return <AuctionResults />;
      case STAGE_SCREENS_ENUM.SECOND_ROUND:
        return <AuctionBiddingSecondRound />;
      case STAGE_SCREENS_ENUM.FIRST_ROUND:
        return <AuctionBiddingFirstRound />;

      case STAGE_SCREENS_ENUM.WAIT_FIRST_ROUND:
      case STAGE_SCREENS_ENUM.WAIT_SECOND_ROUND:
        return <BiddingPreparation />;

      case STAGE_SCREENS_ENUM.SELECTION:
      case STAGE_SCREENS_ENUM.CART:
      default:
        return <AuctionCart />;
    }
  }, [auctionState.currentStage]);

  return (
    <div ref={wrapperRef} className={'widget-app-wrapper'}>
      <p className={'absolute bottom-1 left-1 !hidden'}>Version 2</p>
      <div
        onClick={() => setIsOpen(false)}
        className={cn('fixed inset-0 bg-black opacity-25 z-[9998]', {
          '!block': isOpen,
          hidden: !isOpen,
        })}
      />

      <FloatTriggers
        className={cn({
          hidden: isOpen,
        })}
      />

      <div
        onClick={(e) => e.stopPropagation()}
        className={cn(
          'widget-container flex-col w-full flex h-screen bg-white border border-gray-200 shadow-lg z-[9999]',
          'fixed top-0 bottom-0 right-0 h-screen overflow-auto transition duration-300',
          isDoubleScreen ? 'sm:max-w-151' : 'sm:max-w-73',
          {
            'opacity-0 pointer-events-none invisible': !isOpen,
          },
        )}
      >
        <Button
          className={'absolute top-3 sm:top-2 right-3'}
          variant={'icon'}
          onClick={() => setIsOpen(false)}
          aria-label='Close'
        >
          <XIcon width='20' height='20' className={'text-foreground-primary'} />
        </Button>

        <LocaleToggle
          className={
            'absolute top-3 sm:top-2 left-1 pointer-events-none opacity-0'
          }
        />

        <Screen />
      </div>
    </div>
  );
}
