import { FC, useContext } from 'react';
import InfoDialog from '../ui/dialogs/info-dialog.tsx';
import ButtonInfo from '../ui/buttons/button-info.tsx';
import ButtonCart from '../ui/buttons/button-cart.tsx';
import Button from '../ui/buttons/button.tsx';
import { HourglassIcon } from '../icons';
import { WidgetContext } from '../lib/context.ts';
import cn from 'classnames';
import { useI18n } from '../hooks/use-i18n.ts';

export interface FloatTriggersProps {
  className?: string;
}

const FloatTriggers: FC<FloatTriggersProps> = ({ className }) => {
  const {
    auctionState,
    cart,
    isInfoOpen,
    isAuctionStarting,
    setIsOpen,
    setIsInfoOpen,
    handleAddVariationToCart,
  } = useContext(WidgetContext);
  const { t } = useI18n();

  const handleOpenAuctionWidget = () => {
    setIsOpen(true);
  };

  return (
    <div>
      {isInfoOpen && <InfoDialog onClose={() => setIsInfoOpen(false)} />}
      <div
        className={cn(
          'fixed bottom-0 right-0 flex flex-col flex-wrap max-w-full items-end gap-2 z-[9999] p-4',
          className,
        )}
      >
        {!isInfoOpen && <ButtonInfo onClick={() => setIsInfoOpen(true)} />}
        <div className='flex gap-2 flex-wrap max-w-full'>
          {cart?.length > 0 && (
            <ButtonCart
              cartQuantity={cart.length}
              onClick={handleOpenAuctionWidget}
            />
          )}
          <Button
            className={'relative h-14'}
            onClick={handleAddVariationToCart}
          >
            {(auctionState.isAuctionRunning || isAuctionStarting) && (
              <span
                className={
                  'w-3.5 h-auto aspect-square rounded-full bg-error absolute top-0.5 right-0.5 border-2 border-white'
                }
              />
            )}
            <HourglassIcon width='18' height='20' />
            {!auctionState.isAuctionRunning && !isAuctionStarting && (
              <span className={'font-normal'}>
                {t('buttons.by-instant-auction')}
                {/*Buy on Instant Auction*/}
              </span>
            )}
          </Button>
        </div>
      </div>
    </div>
  );
};

FloatTriggers.displayName = 'FloatTriggers';

export default FloatTriggers;
