import type { ComponentProps, FC } from 'react';
import { BagsShoppingIcon } from '../../icons';
import cn from 'classnames';

export interface ButtonCartProps extends ComponentProps<'button'> {
  cartQuantity?: number | string;
}

const ButtonCart: FC<ButtonCartProps> = ({
  className,
  cartQuantity = 0,
  ...rest
}) => {
  return (
    <div className='p-[1px] rounded-[32px] bg-gradient-primary'>
      <button
        className={cn(
          'relative bg-background-primary flex items-center justify-center h-14 w-14 text-background-primary border-none rounded-[32px] cursor-pointer',
          className,
        )}
        {...rest}
      >
        <BagsShoppingIcon
          width='20'
          height='20'
          className={'text-foreground-primary'}
        />
        {!!cartQuantity && (
          <span className='bg-error h-5 w-5 rounded-full text-xs flex items-center justify-center absolute right-0 top-0'>
            {cartQuantity}
          </span>
        )}
      </button>
    </div>
  );
};

ButtonCart.displayName = 'ButtonCart';

export default ButtonCart;
