import { FC, useContext, useEffect, useMemo, useState } from 'react';
import Logo from '../ui/logo.tsx';
import { WidgetContext } from '../lib/context.ts';
import ProgressCircle from '../ui/progress/progress-circle.tsx';
import Button from '../ui/buttons/button.tsx';
import SpeakerOn from '../icons/speaker-on.tsx';
import SpeakerOff from '../icons/speaker-off.tsx';
import Title from '../ui/typography/title.tsx';
import ListProducts from '../components/list-products.tsx';
import AuctionPrice from '../ui/auction-price.tsx';
import { useScreenCredentials } from '../hooks/use-screen-credentials.ts';
import { ClampText } from '../ui/typography/clamp-text.tsx';
import { useI18n } from '../hooks/use-i18n.ts';
import { WS_EVENT_ENUM } from '../constants/ws-event-enum.ts';

export interface AuctionBiddingProps {}

const AuctionBiddingFirstRound: FC<AuctionBiddingProps> = () => {
  const { auctionState, handleAuctionSendMessage } = useContext(WidgetContext);
  const [isSoundOff, setIsSoundOff] = useState(false);
  const { isMobile } = useScreenCredentials();
  const [isPriceCatch, setIsPriceCatch] = useState(false);
  const [resetTrigger, setResetTrigger] = useState<number>(Date.now());
  const { t } = useI18n();

  useEffect(() => {
    setResetTrigger(Date.now());
  }, [auctionState.roundStepMessage]);

  useEffect(() => {
    if (auctionState.errorEvent === WS_EVENT_ENUM.BET) {
      setIsPriceCatch(false);
    }
  }, [auctionState.errorEvent]);

  const { productList, customerProducts } = useMemo(() => {
    const { products: customerProducts = [] } =
      auctionState.selectionMessage?.data ?? {};
    const currentCustomer = auctionState.currentCustomer;
    const { members = [] } = auctionState.waitRoundMessage?.data ?? {};
    const { others = {}, price } = auctionState.roundStepMessage?.data ?? {};

    const customerProduct = customerProducts[0];

    const filteredMembers = members.filter(
      (member) =>
        member.sessionId !== auctionState?.selectionMessage?.data.sessionId,
    );

    const response = Object.entries(others).map(([id, price]) => {
      const member = filteredMembers.find((m) => m.sessionId === id);
      if (!member) {
        return;
      }

      return {
        imgUrl: member?.products[0]?.imageUrl,
        imgAlt: member?.products[0]?.name,
        price: price,
        currencyCode: member.currency,
        userName: member.name,
        userLocation: member.location,
      };
    });

    return {
      productList: response.filter((item): item is NonNullable<typeof item> =>
        Boolean(item),
      ),
      customerProducts: [
        {
          imgUrl: customerProduct?.imageUrl,
          imgAlt: customerProduct?.name,
          price: price,
          currencyCode: currentCustomer?.currency,
          userName: currentCustomer?.name,
          userLocation: currentCustomer?.location,
        },
      ],
    };
  }, [auctionState]);

  const handleCatchPrice = () => {
    const hasError = handleAuctionSendMessage({
      command: WS_EVENT_ENUM.BET,
      auctionId: auctionState.waitRoundMessage?.data.auctionId,
      round: 1,
      sessionId: auctionState.selectionMessage?.data.sessionId,
    });

    if (!hasError) {
      setIsPriceCatch(true);
    }
  };

  return (
    <div className='flex flex-col-reverse sm:flex-row flex-grow bg-background-primary text-foreground-primary'>
      <div className='widget-screen sm:bg-background-secondary sm:text-foreground-secondary pt-6 !pb-4 sm:basis-1/2 sm:w-1/2 sm:max-w-73'>
        <div className='widget-screen-content'>
          {!isMobile && (
            <div className={'flex gap-7 mb-2'}>
              <Button
                className={'rounded-full p-0'}
                style={{
                  width: '40px',
                  height: '40px',
                }}
                variant={'secondary'}
                onClick={() => setIsSoundOff((p) => !p)}
              >
                {isSoundOff ? (
                  <SpeakerOff width={24} height={24} />
                ) : (
                  <SpeakerOn width={24} height={24} />
                )}
              </Button>
              <ProgressCircle
                resetTrigger={resetTrigger}
                classNameWrapper={'mx-auto'}
                holdCircleColor={'primary'}
                delay={auctionState.waitRoundMessage?.data.stepDelay ?? 3}
                initialProgress={0}
              />

              <div className={'w-10 h-10 !block'} />
            </div>
          )}

          <Title className={'font-normal'} size={'md'}>
            {t('first-round.other-bidders')} ({productList.length})
          </Title>

          <ListProducts list={productList} />
        </div>
        <Logo />
      </div>

      <div className='widget-screen pt-6 sm:basis-1/2 sm:w-1/2 max-sm:!flex-grow-0'>
        <div className='widget-screen-content max-sm:!flex-grow-0'>
          <Title>
            {t('first-round.title')}
            {/*Auction bidding round 1*/}
          </Title>

          {isMobile && (
            <div className={'flex gap-7 mb-3'}>
              <Button
                className={'rounded-full p-0'}
                style={{
                  width: '40px',
                  height: '40px',
                }}
                variant={'secondary'}
                onClick={() => setIsSoundOff((p) => !p)}
              >
                {isSoundOff ? (
                  <SpeakerOff width={24} height={24} />
                ) : (
                  <SpeakerOn width={24} height={24} />
                )}
              </Button>
              <ProgressCircle
                resetTrigger={resetTrigger}
                classNameWrapper={'mx-auto'}
                holdCircleColor={'secondary'}
                delay={auctionState.waitRoundMessage?.data.stepDelay ?? 3}
                initialProgress={0}
              />

              <div className={'w-10 h-10 !block'} />
            </div>
          )}

          <div className={'flex flex-col gap-2'}>
            <AuctionPrice
              title={t('first-round.auction-price.title')}
              // title={'Starting price'}
              description={t('first-round.auction-price.description')}
              // description={'Become the only one to grab the lowest price!'}
              price={
                auctionState.roundStepMessage?.data.price as unknown as string
              }
              currencyCode={
                auctionState.selectionMessage?.data.currency as string
              }
              disabled={isPriceCatch || auctionState.isFirstRoundWinner}
              onClick={handleCatchPrice}
              vector={'down'}
            />

            <ul
              className={
                'sm:grid sm:grid-cols-2 flex gap-2 w-full overflow-x-auto max-sm:pb-1'
              }
            >
              {[...customerProducts, ...productList].map((product) => (
                <li
                  key={product.imgUrl + 'img'}
                  className={
                    'flex items-center justify-center w-full h-auto aspect-square border border-border rounded-2xl p-2 sm:p-4 max-sm:min-w-20'
                  }
                >
                  <img
                    className={'w-full h-full object-contain'}
                    src={product.imgUrl}
                    alt={product.imgAlt}
                    width={101}
                    height={101}
                  />
                </li>
              ))}
            </ul>
          </div>

          <ClampText lines={3} hasToggleButton={true}>
            {t('first-round.description')}
            {/*Lorem ipsum dolor sit amet consectetur. Neque ipsum enim bibendum*/}
            {/*nisl ipsum viverra sed. Maecenas ac consequat quisque. Lorem ipsum*/}
            {/*dolor sit amet consectetur. Neque ipsum enim bibendum nisl ipsum*/}
            {/*viverra sed. Maecenas ac consequat quisque. Lorem ipsum dolor sit*/}
            {/*amet consectetur. Neque ipsum enim bibendum nisl ipsum viverra sed.*/}
            {/*Maecenas ac consequat quisque*/}
          </ClampText>
        </div>
      </div>
    </div>
  );
};

AuctionBiddingFirstRound.displayName = 'AuctionBiddingFirstRound';

export default AuctionBiddingFirstRound;
