import type { FC } from 'react';
import { LogoIcon } from '../icons';
import cn from 'classnames';

export interface LogoProps {
  className?: string;
  onClick?: () => void;
  onContextMenu?: () => void;
}

const Logo: FC<LogoProps> = ({ className, onClick, onContextMenu }) => {
  return (
    <LogoIcon
      width='196'
      height='38'
      className={cn('text-grayscale-dark mx-auto', className)}
      onClick={onClick}
      onContextMenu={(event) => {
        event.preventDefault();
        onContextMenu?.();
      }}
    />
  );
};

Logo.displayName = 'Logo';

export default Logo;
