import type { FC, PropsWithChildren } from 'react';
import { useEffect, useRef, useState } from 'react';
import cn from 'classnames';
import Paragraph from './paragraph.tsx';
import { useI18n } from '../../hooks/use-i18n.ts';

const linesClasses = {
  1: '[-webkit-line-clamp:1]',
  3: '[-webkit-line-clamp:3]',
  5: '[-webkit-line-clamp:5]',
  10: '[-webkit-line-clamp:10]',
  15: '[-webkit-line-clamp:15]',
};

export interface ClampTextProps extends PropsWithChildren {
  lines?: keyof typeof linesClasses;
  hasToggleButton?: boolean;
}

export const ClampText: FC<ClampTextProps> = ({
  children,
  lines = 5,
  hasToggleButton = false,
}) => {
  const [isOpen, setIsOpen] = useState<boolean>(false);
  const [isOverflowing, setIsOverflowing] = useState<boolean>(false);
  const paragraphRef = useRef<HTMLDivElement>(null);
  const { t } = useI18n();

  const handleToggle = () => {
    setIsOpen((prev) => !prev);
  };

  useEffect(() => {
    const handleResize = () => {
      const paragraph = paragraphRef.current;
      if (!paragraph || !hasToggleButton) {
        return;
      }

      if (isOpen) {
        setIsOverflowing(true);
        return;
      }

      setIsOverflowing(paragraph.scrollHeight > paragraph.clientHeight);
    };

    handleResize();
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, [children, isOpen, hasToggleButton]);

  return (
    <div className={'flex flex-col gap-2'}>
      <div
        className={cn(
          !isOpen && linesClasses[lines],
          !isOpen &&
            `[display:-webkit-box] [-webkit-box-orient:vertical] [overflow:hidden]`,
        )}
      >
        <div ref={paragraphRef}>
          <Paragraph className={'text-sm leading-5'} variant={'gray'}>
            {children}
          </Paragraph>
        </div>
      </div>

      {hasToggleButton && isOverflowing && (
        <button
          className={
            'flex gap-1.5 items-center justify-center w-fit h-fit p-0 no-underline font-normal text-accent text-sm leading-4'
          }
          type={'button'}
          onClick={handleToggle}
        >
          <span>{t(isOpen ? 'buttons.hide' : 'buttons.show-more')}</span>
        </button>
      )}
    </div>
  );
};
