import { createElement, DetailedHTMLProps, FC, HTMLProps } from 'react';
import cn from 'classnames';

const paragraphSizes = {
  base: 'text-base',
  xs: 'text-xs',
  lg: 'text-sm leading-5',
};

const paragraphVariants = {
  error: 'text-error',
  gray: 'text-grayscale-bold',
  gradient: 'text-with-gradient',
};

type ParagraphSizes = keyof typeof paragraphSizes;
type ParagraphVariants = keyof typeof paragraphVariants;
type ParagraphTags = 'p' | 'span';

export interface ParagraphProps
  extends Omit<
    DetailedHTMLProps<HTMLProps<HTMLParagraphElement>, HTMLParagraphElement>,
    'size'
  > {
  tag?: ParagraphTags;
  variant?: ParagraphVariants;
  size?: ParagraphSizes;
}

const Paragraph: FC<ParagraphProps> = ({
  className,
  tag = 'p',
  variant,
  size = 'base',
  ...rest
}) => {
  return createElement(tag, {
    className: cn(
      className,
      paragraphSizes[size],
      variant && paragraphVariants[variant],
    ),
    ...rest,
  });
};

Paragraph.displayName = 'Paragraph';

export default Paragraph;
