import { createElement, DetailedHTMLProps, FC, HTMLProps } from 'react';
import cn from 'classnames';

const titleVariants = {
  error: 'text-error',
  gray: 'text-grayscale-bold',
  gradient: 'text-with-gradient',
};

const titleSizes = {
  sm: 'text-xs leading-[14px]',
  md: 'text-base leading-5',
  xl: 'text-xl font-semibold leading-6',
  '3xl': 'text-2.5xl leading-8.5 font-semibold',
};

type TitleSizes = keyof typeof titleSizes;
type TitleVariants = keyof typeof titleVariants;
type TitleTags = 'h1' | 'h2' | 'h3' | 'h4' | 'h5' | 'h6' | 'p' | 'span';

export interface TitleProps
  extends Omit<
    DetailedHTMLProps<HTMLProps<HTMLHeadingElement>, HTMLHeadingElement>,
    'size'
  > {
  tag?: TitleTags;
  size?: TitleSizes;
  variant?: TitleVariants;
}

const Title: FC<TitleProps> = ({
  className,
  tag = 'h2',
  variant,
  size = 'xl',
  ...rest
}) => {
  return createElement(tag, {
    className: cn(
      'text-foreground-primary',
      titleSizes[size],
      variant && titleVariants[variant],
      className,
    ),
    ...rest,
  });
};

Title.displayName = 'Title';

export default Title;
