import '../styles/style.css';

import { useEffect, useState } from 'react';
import { WidgetContext } from '../lib/context';
import { Widget } from './widget';
import { IWidgetOptions } from '../types/widget-options.ts';
import { useAuction } from '../hooks/use-auction.ts';
import { useI18n } from '../hooks/use-i18n.ts';
import { createPortal } from 'react-dom';
import { useWidgetState } from '../hooks/use-widget-state.ts';

export interface HobuyWidgetProps extends IWidgetOptions {}

export function HobuyWidget({
  socketUrl,
  locale,
  injectToBody = true,
  product,
  shopCurrency,
  customerName,
  onStartAuction,
  onUseWinData,
}: HobuyWidgetProps) {
  const [isMounted, setIsMounted] = useState(false);
  const [wssUrl, setWssUrl] = useState<string | undefined>(socketUrl);
  const {
    state,
    handleSetIsOpen,
    handleSetIsInfoOpen,
    handleAddVariationToCart,
    handleStartAuction,
  } = useWidgetState({
    product,
    shopCurrency,
    customerName,
    onStartAuction,
    setSocketUrl: setWssUrl,
  });
  const { setLang } = useI18n();
  const {
    auctionSocket,
    auctionState,
    handleAuctionSendMessage,
    handleResetAuction,
  } = useAuction({
    url: wssUrl,
  });

  const handleUseWinData = () => {
    onUseWinData?.(auctionState.winData);
  };

  useEffect(() => {
    setWssUrl(wssUrl);
  }, [wssUrl]);

  useEffect(() => {
    if (locale) {
      setLang(locale);
    }
  }, [locale]);

  useEffect(() => {
    setIsMounted(true);
  }, []);

  if (!isMounted) {
    return null;
  }

  return (
    <WidgetContext.Provider
      value={{
        auctionSocket,
        auctionState,
        cart: state.cart,
        customerName: state.customerName,
        currency: state.currency,
        isAuctionStarting: state.isAuctionStarting,
        isOpen: state.isOpen,
        isInfoOpen: state.isInfoOpen,
        setIsOpen: handleSetIsOpen,
        setIsInfoOpen: handleSetIsInfoOpen,
        handleAuctionSendMessage,
        handleResetAuction,
        handleAddVariationToCart,
        handleStartAuction,
        handleUseWinData,
      }}
    >
      {injectToBody ? createPortal(<Widget />, document.body) : <Widget />}
    </WidgetContext.Provider>
  );
}
