import type { FC } from 'react';
import Title from './typography/title.tsx';
import Paragraph from './typography/paragraph.tsx';
import { getPriceWithCurrency } from '../utils/currency';
import { DownArrowIcon } from '../icons';
import cn from 'classnames';
import Button from './buttons/button.tsx';
import { isNullable } from '../utils/is-nullable.ts';
import { useI18n } from '../hooks/use-i18n.ts';

export interface AuctionPriceProps {
  price: number | string;
  currencyCode: string;
  title: string;
  description: string;
  vector?: 'up' | 'down';
  borderColor?: 'gradient' | 'gray';

  onClick?: () => void;
  disabled?: boolean;
}

const AuctionPrice: FC<AuctionPriceProps> = ({
  price,
  currencyCode,
  vector = 'up',
  borderColor = 'gray',
  description,
  title,
  onClick,
  disabled = false,
}) => {
  const { t } = useI18n();

  return (
    <div
      className={cn('rounded-2xl border relative p-[1px]', {
        'border-border': borderColor === 'gray',
        'border-transparent before:block before:w-full before:h-full before:rounded-2xl before:bg-gradient-primary before:absolute before:top-0 before:left-0 before:z-[1]':
          borderColor === 'gradient',
      })}
    >
      <div
        className={
          'relative w-full h-full p-4 bg-background-primary z-[2] rounded-2xl'
        }
      >
        <Title className={'mb-2'} size={'sm'} tag={'p'}>
          {title}
        </Title>

        <div className={'flex gap-2 items-center mb-1'}>
          <Paragraph
            className={'!text-2.5xl leading-8.5 font-semibold'}
            variant={'gradient'}
          >
            {!isNullable(price) && getPriceWithCurrency(price, currencyCode)}
          </Paragraph>

          <div
            className={
              'flex items-center justify-center w-5 h-5 rounded-full bg-accent/25'
            }
          >
            <DownArrowIcon
              width={12}
              height={12}
              className={cn('text-accent', {
                'rotate-180': vector === 'up',
              })}
            />
          </div>
        </div>

        <Paragraph className={'text-sm leading-4 mb-4'} variant={'gray'}>
          {description}
        </Paragraph>

        <Button className={'w-full'} onClick={onClick} disabled={disabled}>
          {t('buttons.catch-price')}
        </Button>
      </div>
    </div>
  );
};

AuctionPrice.displayName = 'AuctionPrice';

export default AuctionPrice;
